/* src/styles/CenteredLogos.css */

.centered-logos {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px; /* Adjust the gap between logos if necessary */
    margin-top: 10vh; /* Adjust the top margin to position logos vertically centered */
  }
  
  .centered-logos .logo {
    height: 200px; /* Adjust the size of the logos if necessary */
  }
  